@import url(https://fonts.googleapis.com/css?family=Lobster);
@import url(https://fonts.googleapis.com/css?family=Sedgwick+Ave);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-header {
  background-color: #343a40;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

@media (orientation: landscape) and (max-height: 720px) {
  .App-header {
    height: 130vh;
  }
}

.App-link {
  color: #61dafb;
}

.RosterMain {
  margin: 10px 5px 5px 10px;
}

.roster ul {
  display: inline-block;
  margin: 0;
  padding: 0;
  min-width: -webkit-fill-available;
}

.roster li {
  padding: 10px 5px;
  margin: 5px 5px 5px 10px;
  text-align: center;
  font-family: "Sedgwick Ave", Helvetica, sans-serif;
  font-size: 1.2em;
}

.theList {
  list-style: none;
  background-color: ghostwhite;
}

.lineupList {
  display: inline-block;
  list-style: none;
}

.lineupCard {
  height: 150%;
}

.lineupSidebar {
  font-size: 20px;
}

.male {
  background-color: rgb(192, 229, 241, 0.5);
  width: 95%;
  min-height: 50px;
  border-style: dashed;
  margin: 5px 5px 5px 5px;
}

.male.selected {
  opacity: 50%;
}

.female {
  background-color: rgb(230, 230, 247, 0.5);
  width: 95%;
  min-height: 50px;
  border: dashed;
  margin: 5px 5px 5px 5px;
}

.female.selected {
  opacity: 50%;
}

table thead th {
  text-align: center;
}

table td.toggleSquare {
  min-width: 60px;
}

.scoreboardOuts {
  font-size: 200%;
}

#rosterButton {
  margin-bottom: 25px;
}

#toggleForm {
  margin: 0px 0px 15px 10px;
}

#playerForm {
  text-align: left;
}

#playerForm.default {
  height: 0px;
  overflow: auto;
  -webkit-transition: height 1s;
  transition: height 1s;
}

#playerForm.active {
  height: 150px;
  overflow: auto;
  -webkit-transition: height 1s;
  transition: height 1s;
}

#submitPlayerForm {
  text-align: center;
}

input[type="radio"] {
  margin: 4px 5px 0px;
}

input[type="text"] {
  width: 75%;
}

/** Moble **/
@media screen and (max-width: 600px) {
  #playerForm.active {
    height: 210px;
  }
}

* {
  margin: 0;
  padding: 0;
  position: relative;
  box-sizing: border-box;
}

html, body {
  height: 100%;
}

.b {
  display: block;
}

.toggle {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 60px;
  height: 40px;
  border-radius: 100px;
  background-color: #ddd;
  margin: -20px -40px;
  overflow: hidden;
  box-shadow: inset 0 0 2px 1px rgba(0,0,0,.05);
}

.check {
  position: absolute;
  display: block;
  cursor: pointer;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  z-index: 6;
}

  .check:checked ~ .track {
    box-shadow: inset 0 0 0 20px #4bd863;
  }

  .check:checked ~ .switch {
    right: 2px;
    left: 22px;
    -webkit-transition: .35s cubic-bezier(0.785, 0.135, 0.150, 0.860);
    transition: .35s cubic-bezier(0.785, 0.135, 0.150, 0.860);
    -webkit-transition-property: left, right;
    transition-property: left, right;
    -webkit-transition-delay: .05s, 0s;
            transition-delay: .05s, 0s;
  }

.switch {
  position: absolute;
  left: 2px;
  top: 2px;
  bottom: 2px;
  right: 22px;
  background-color: #fff;
  border-radius: 36px;
  z-index: 1;
  -webkit-transition: .35s cubic-bezier(0.785, 0.135, 0.150, 0.860);
  transition: .35s cubic-bezier(0.785, 0.135, 0.150, 0.860);
  -webkit-transition-property: left, right;
  transition-property: left, right;
  -webkit-transition-delay: 0s, .05s;
          transition-delay: 0s, .05s;
  box-shadow: 0 1px 2px rgba(0,0,0,.2);
}

.track {
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  -webkit-transition: .35s cubic-bezier(0.785, 0.135, 0.150, 0.860);
  transition: .35s cubic-bezier(0.785, 0.135, 0.150, 0.860);
  box-shadow: inset 0 0 0 2px rgba(0,0,0,.05);
  border-radius: 40px;
}

.toggleLabelLeft {
    margin-right: 30px;
}

.toggleLabelRight {
    margin-left: 10px;
}

table.myTable {
  border: 1px dashed;
  border-collapse: collapse;
  margin: auto;
  width: -webkit-fill-available;
}

.myTable th {
  border: 1px dashed;
  text-align: left;
  padding: 5px 15px 5px 15px;
  font-size: 2em;
  font-family: "Lobster";
}
.myTable td {
  border: 1px dashed;
  padding: 5px 0px 5px 5px;
  height: 100px;
}

table .countTable {
  border: none;
  border-collapse: collapse;
  padding: 0px;
  position: absolute;
  right: 5px;
  top: -5px;
}

.countTable td {
  border: 1px solid;
  height: 15px;
  width: 15px;
}

td .strike3 {
  border: none;
}

.nameCell {
  padding-right: 10px;
  font-family: "Sedgwick Ave", Helvetica, sans-serif;
  font-size: 2.5em;
  width: 50%;
}

.field {
  position: absolute;
  top: -55px;
  left: 30px;
  height: 75px;
  width: 75px;
  border-radius: 1% 80% 1% 1%;
  border: 1px dashed;
  background-color: white;
  -webkit-transform: rotate(-45deg);
}

.infield {
  position: absolute;
  top: -7px;
  left: 50px;
  height: 35px;
  width: 35px;
  border-radius: 1% 1% 1% 1%;
  border: 1px dashed; /*#9b7653; */
  background-color: white; /*#9b7653;*/
  -webkit-transform: rotate(-45deg);
}

.stadium {
  position: relative;
  width: 200px;
}

.lineupUp {
  margin-top: 0px;
}

.lineupDown {
  margin-bottom: 5px;
}

/** Mobile **/

@media screen and (max-width: 600px) {
  .myTable th {
    font-size: 1.5em;
  }

  .field {
    top: -50px;
    left: 5px;
    height: 35px;
    width: 35px;
  }

  .infield {
    position: absolute;
    top: -25px;
    left: 15px;
    height: 15px;
    width: 15px;
  }

  .countTable td {
    height: 10px;
    width: 10px;
  }
}

.scoreboard {
  display: flex;
  width: 100%;
}

table.greenTable {
  font-family: Georgia, serif;
  border: 6px solid #24943A;
  background-color: #D4EED1;
  width: 100%;
  text-align: center;
  color: black;
}
table.greenTable th {
  border: 1px solid #24943A;
  padding: 3px 2px;
  width: 200px;
  text-align: center;
  font-size: 3em
}

table.greenTable td {
    border: 1px solid #24943A;
    padding: 3px 2px;
    width: 100px;
}
table.greenTable tbody td {
  font-size: 1.3em;
}
table.greenTable thead {
  background: #24943A;
  background: -webkit-linear-gradient(top, #5baf6b 0%, #3a9e4d 66%, #24943A 100%);
  background: -webkit-gradient(linear, left top, left bottom, from(#5baf6b), color-stop(66%, #3a9e4d), to(#24943A));
  background: linear-gradient(to bottom, #5baf6b 0%, #3a9e4d 66%, #24943A 100%);
  border-bottom: 0px solid #444444;
}
table.greenTable thead th {
  font-size: 30px;
  font-weight: bold;
  color: #F0F0F0;
  text-align: center;
  border-left: 2px solid #24943A;
}
table.greenTable thead th:first-child {
  border-left: none;
}

table.greenTable tfoot {
  font-size: 13px;
  font-weight: bold;
  color: #F0F0F0;
  background: #24943A;
  background: -webkit-linear-gradient(top, #5baf6b 0%, #3a9e4d 66%, #24943A 100%);
  background: -webkit-gradient(linear, left top, left bottom, from(#5baf6b), color-stop(66%, #3a9e4d), to(#24943A));
  background: linear-gradient(to bottom, #5baf6b 0%, #3a9e4d 66%, #24943A 100%);
  border-top: 1px solid #24943A;
}
table.greenTable tfoot td {
  font-size: 13px;
}
table.greenTable tfoot .links {
  text-align: right;
}
table.greenTable tfoot .links a{
  display: inline-block;
  background: #FFFFFF;
  color: #24943A;
  padding: 2px 8px;
  border-radius: 5px;
}

table.hiddenTable td {
  border-collapse: collapse;
  border: none;
}

button.tableButton .btn .btn-secondary {
  margin-left: 5px solid;
  text-align: center;
}

table.table .table-dark {
  table-layout: fixed;
}

table.scoreTable {
  color: white;
}
